<template>
  <div class="popup" v-if="show">
    <div class="popup__wrap">
      <div class="popup__header">
        <p v-if="phone">Подтверждение номера</p>
        <p v-else>Подтверждение СМС</p>

        <svg width="15" height="15" viewBox="0 0 15 15" fill="none" xmlns="http://www.w3.org/2000/svg" class="popup__close" @click="closePopup">
          <path d="M8.70697 7.6265L13.9414 2.39186C14.0854 2.24777 14.1648 2.05552 14.165 1.85053C14.165 1.64542 14.0856 1.45295 13.9414 1.30908L13.4827 0.850498C13.3385 0.706059 13.1462 0.626953 12.941 0.626953C12.7361 0.626953 12.5439 0.706059 12.3997 0.850498L7.16527 6.08479L1.93063 0.850498C1.78665 0.706059 1.59429 0.626953 1.38919 0.626953C1.18431 0.626953 0.99195 0.706059 0.847966 0.850498L0.389039 1.30908C0.0903724 1.60775 0.0903724 2.09354 0.389039 2.39186L5.62356 7.6265L0.389039 12.8609C0.244942 13.0052 0.165608 13.1975 0.165608 13.4025C0.165608 13.6075 0.244942 13.7997 0.389039 13.9439L0.847852 14.4025C0.991836 14.5468 1.18431 14.626 1.38907 14.626C1.59418 14.626 1.78654 14.5468 1.93052 14.4025L7.16515 9.16809L12.3996 14.4025C12.5438 14.5468 12.736 14.626 12.9409 14.626H12.9411C13.1461 14.626 13.3384 14.5468 13.4826 14.4025L13.9413 13.9439C14.0853 13.7998 14.1647 13.6075 14.1647 13.4025C14.1647 13.1975 14.0853 13.0052 13.9413 12.861L8.70697 7.6265Z" fill="#454545"/>
        </svg>
      </div>
      <div class="popup__content">
        <div v-if="phone">
          <p> class="popup__text">Введите последние 4 цифры вашего номера привязанного к номеру бронирования</p>
          <span>+7 909 943 ** **</span>
        </div>
        
        <div v-else>
          <p class="popup__text">Мы отправили СМС с кодом подтверждения на Ваш номер телефона.</p>
          <p class="popup__text">Пожалуйста ввердите данный код ниже.</p>
        </div>

        <input v-if="phone" v-maska="'####'" type="text" class="form-control">
        <input v-else v-maska="'#####'" type="text" class="form-control">

        <span v-if="!phone" class="popup__time">Код действителен: <strong>00:{{time}}</strong></span>

        <router-link to="menu"><button class="btn">Подтвердить</button></router-link>
      </div>

    </div>
  </div>
</template>

<script>
export default {

  name: 'confirmationPopup',

  props: {
    show: {
      require: true,
      type: Boolean
    },
    timer: Boolean,
    phone: Boolean,
  },
  
  data() {
    return {
      time: 59,
    }
  },

  methods: {
    closePopup() {
      this.$emit("closeConfirmPopup", false)
    },
  },
  watch: {
    show() {
      if (this.timer) {
        clearInterval(this.timeInterval)
        this.timeInterval = setInterval(() => this.time--, 1000)
      }
    },

    time() {
      if (this.time == 0 && this.show && this.show) {
        clearInterval(this.timeInterval)
      }
    },
  }
}
</script>

