import { createRouter, createWebHistory } from 'vue-router'
import CabinetLogin from '../views/CabinetLogin'
import Cabinet from '../views/Cabinet'
import NotFound from '../views/NotFound'

const routes = [

  {
    path: '/',
    name: 'CabinetLogin',
    component: CabinetLogin
  },
  {
    path: '/not-found',
    name: 'notFound',
    component: NotFound,
  },
  {
    path: '/cabinet',
    name: 'cabinet',
    component: Cabinet,
    
  }
  // {
  //   path: '/:pathMatch(.*)*',
  //   redirect: {name: 'notFound'}
  // },
  // {
  //   path: '/qr/:hash',
  //   name: 'other',
  //   redirect: {name: 'login'}
  // },
]
//"/room_service_extended/"
const router = createRouter({
  history: createWebHistory("/room_service_extended/"),
  routes
})

export default router