<template>
    <!-- header -->
    <header class="header">
      <div class="header__wrap">
        <router-link to="/" class="header__logo">Управление бронированием</router-link>

        <lang-switcher />
      </div>
    </header>
    <!-- ./header -->

    <!-- login content -->
    <div class="login">
      <div class="container">
        <div class="login__wrap">
          <div class="login__header">
            <img  src="@/assets/icons/logo.svg" alt="Логотип" class="login__icon">

            <h2 class="login__text">Пожалуйста, укажите <br>номер брони и код из SMS.</h2>
            <p class="login__subtext">Номер бронирования и код доступа <br>вы можете найти в SMS сообщении от отеля.</p>
          </div>

          <form class="form" action="#" @submit.prevent>
            <input
              class="form-control"
              v-maska="'########-#####-########'"
