            >

            <input
              class="form-control numberAccess"
              placeholder="Код доступа"
              v-maska="'XXXXXXXXXX'"
              v-model="numberAccess"
              type="tel"
            >

            <button
              class="btn "
              @click="recaptcha"
              >Управлять бронированием
            </button>
          </form>
        </div>
      </div>
    </div>
    <!-- ./login content -->

    <!-- footer -->
    <footer class="footer">
      <div class="container">
        <div class="footer__wrap">
          <span class="footer__text">
            Если у вас есть вопрос другого <br>характера, просим <a href="#">связаться с поддержкой</a>
          </span>

          <img src="@/assets/icons/footer-icon.svg" alt="">
        </div>
      </div>
    </footer>
    <!-- ./footer -->

  <confirmationPopup :show="confirmPopupShow" class="sms" @closeConfirmPopup="closeConfirmPopup" phone/>
  <confirmationPopup :show="popupShow" class="sms" @closePopup="closePopup" timer/>

  <v-snack-bar
    :show="snackbarShow"
    :config="snacks[snack]"
  />
</template>

<script>
import ConfirmationPopup from "@/components/ConfirmationPopup"
import LangSwitcher from "@/components/LangSwitcher"
import VSnackBar from "@/components/VSnackBar"
const setCookie = (name, value, options = {}) => {

  options = {
    path: '/',
    // при необходимости добавьте другие значения по умолчанию
    ...options
  };

  if (options.expires instanceof Date) {
    options.expires = options.expires.toUTCString();
  }

  let updatedCookie = encodeURIComponent(name) + "=" + encodeURIComponent(value);

  for (let optionKey in options) {
    updatedCookie += "; " + optionKey;
    let optionValue = options[optionKey];
    if (optionValue !== true) {
      updatedCookie += "=" + optionValue;
    }
  }

  document.cookie = updatedCookie;
  }

export default {
  name: 'Login',

  components: {
    ConfirmationPopup,
    LangSwitcher,
    VSnackBar
  },

  inject: {
    store: "$store"
  },

  created() {
    this.hash = this.$route.params?.hash
    window.localStorage.removeItem("menu_link")
    window.localStorage.removeItem("cart")

    console.log("login")
  },

  mounted() {
    this.$load(async () => {
    const data = await this.$api.session.sessionInfo()
      .then(res => {
        if (res.data.status) {
          this.$router.push({name: "cabinet"})
        }
      })
    })
  },

  data() {
    return {
      numberResevration: "",
      numberAccess: "",
      qr: "achhjsiGxIMWuXWj8Qpx4zXLKJAzT2EXlGbNfxMzu8clU4dDQ96OTwru3RjeRXEcQcNDYBRt",
      captcha_code: '',

      snackbarShow: false,
      snack: 0,

      snacks: [
        {
          text: "Неправильный телефон или код",
          modificator: "alert"
        },
        {
          text: "Вы не ввели телефон",
          modificator: "alert"
        },
        {
          text: "Подождите отправки данных",
          modificator: "alert"
        },
        {
          text: "Смс устарел",
          modificator: "alert"
        },
        {
          text: "Смс ещё не истёк",
          modificator: "alert"
        },
        {
          text: "Технические проблемы сервера",
          modificator: "alert"
        },
      ],

      confirmPopupShow: false,
      popupShow: false,
      hash: ''
    }
  },

  methods: {
    closeConfirmPopup(closePopup) {
      this.confirmPopupShow = closePopup
    },

    closePopup(closePopup) {
      this.popupShow = closePopup
    },

    login() {
      const body = {
        phone: this.phone,
        booking_id: this.booking_id,
        qr: this.qr,
        captcha_code: this.captcha_code,
      }

      this.$load(async () => {
        const data = await this.$api.auth.signIn(body)
          .then(res => {
            this.$router.push({name: "cabinet"})
            // this.popupShow = true

            this.store.commit("setUser", res.data)

            // Пример использования:
            setCookie('OCSESSID', res.data.session_key, {secure: true, 'max-age': res.data.session_key_expire});
            setCookie('OCSESSID_STAMP', Date.now(), {secure: true, 'max-age': res.data.session_key_expire});
          })
          .catch(err => {

            if (err.response.status == "403") {
              this.snack = 0
            } else if (err.response.status == "400") {
              this.snack = 1
            } else if (err.response.status == "429") {
              this.snack = 2
            } else if (err.response.status == "410") {
              this.snack = 3
            } else if (err.response.status == "425") {
              this.snack = 4
            } else if (err.response.status == "500") {
              this.snack = 5
            }

            this.snackbarShow = true
            setTimeout(()=> this.snackbarShow = false, 2000)
          })
      })
    },

    recaptcha() {
      this.$router.push({name: "cabinet"})
      return
      this.$recaptchaLoaded().then(() => {
        this.$recaptcha("login").then((token) => {
          this.captcha_code = token;
          this.login()
        });
      });
    },
  },
}
</script>

