<script>
import VHeader from "@/components/VHeader"
import InfoPopup from "@/components/InfoPopup"
import ConfirmationPopup from "@/components/ConfirmationPopup"
import { Carousel, Slide, Navigation } from 'vue3-carousel';
import 'vue3-carousel/dist/carousel.css';

// [x] попап Информация о пользователях
// [x] попап из попапа услуги до заезда

// [x] попап баннера и там же проверка номера
// [x] попап из попапа стоимость бронирования

// [ ] доделать попап при нажатии заезд и выезд, стилизовать выбор даты

// [x] попап kudaGo

// [x] доделать начальный попап предложения, facebook и промокод https://adi518.github.io/vue-facebook-login/#/
// [x] попап с картой

// [x] попап баллов

export default {

  name: 'Cabinet',

  components: {
    VHeader,
    Carousel,
    Slide,
    Navigation,
    InfoPopup,
    ConfirmationPopup
  },

  created() {
    // axios.get()
    //  .then(res => {
    //    this.kudaGoEvents = res.data.results
    //  })
  },

  mounted() {
    // let fbSdk = document.createElement('script')
    //     fbSdk.setAttribute('src', 'https://connect.facebook.net/ru_RU/sdk.js')
    //     document.head.appendChild(fbSdk)

    //     FB.init({
    //    appId            : 'room-service',
    //    autoLogAppEvents : true,
    //    xfbml            : true,
    //    version          : 'v11.0'
    //  });
  },

  data () {
    return {
      promotion: window.localStorage.getItem('dontShowPromotion') ? false : true,
      promotionRules: false,
      promotionChecked: false,
      promotionLogined: false,

      payShow: false,
      daysShow: false,
      servicesShow: false,

      breakpoints: {
        // 300px and up
        300: {
          itemsToShow: 1.1,
          snapAlign: 'start',
        },
        // 400 and up
        600: {
          itemsToShow: 2,
          snapAlign: 'start',
        },
      },

      guestsTab: 1,

      infoGuestsState: "show",
      decied: false,
      contactEdit: false,
      showService: false,
      sendDate: false,

      kudaGoEvents: [],

      phone: "+7 495 114-55-72",
      email: "sales@mosshotel.ru",

      dateStart: {},

      slidesBanner: Array(10),
      slidesService: Array(10),
      slidesPopup: Array(10),

      newGuests: {
        id: 2,
        name: "",
        goverment: "",
        age: "adult"
      },

      guests: [
        {
          id: 1,
          name: "Иван Иванов Иванович",
          goverment: "Российская Федерация",
          age: "adult"
        }
      ],

      popups: [
        false, // 0 услуги до заезда
        false, // 1 мои контакты
        false, // 2 мой комментарий
        false, // 3 информация о гостях
        false, // 4 условия отмены
        false, // 5 детали брони
        false, // 6 баннер
        false, // 7 заезд и выезд
        false, // 8 баллы
        false, // 9 KudaGo
        false // 10 карта
      ],

      comment: "",

      popoverConfig: {visibility:"click"},
      pickDate: false,
      popover: 0
    }
  },

  computed: {

    daysBetween() {
      const oneDay = 24 * 60 * 60 * 1000 // hours*minutes*seconds*milliseconds

      return Math.round(Math.abs(((Date.parse(this.dateStart.end) - Date.parse(this.dateStart.start)) / oneDay)))
    },
    dates() {
      const startDate = new Intl.DateTimeFormat('ru-RU', { dateStyle: 'medium'}).format(this.dateStart.start),
            endDate = new Intl.DateTimeFormat('ru-RU', { dateStyle: 'medium'}).format(this.dateStart.end);

      return [
        startDate.slice(0, startDate.indexOf("г.")),
        endDate.slice(0, startDate.indexOf("г."))
      ]
    }
  },

  methods: {
    login() {
      this.promotionRules = false
      this.promotionLogined = true
      // if (promotionChecked) {
      //  if (login = 'fb') {
      //    FB.login(response => {
      //      if (response.authResponse) {
      //        FB.api('/me', response => {
      //         console.log('response: ', response);
      //       });
      //      } else {
      //        console.log('User cancelled login or did not fully authorize.');
      //      }
      //    })
      //  }
      // }
      
    },

    printFile(documentId) {
      const doc = document.getElementById(documentId);

      if (typeof doc.print === 'undefined') {    
        setTimeout(() => this.printFile(documentId), 10000);
      } else {
        let win = window.open();
        self.focus();
        win.document.open();
        win.document.write('<'+'html'+'><'+'body'+'>');
        win.document.write(doc);
        win.document.write('<'+'/body'+'><'+'/html'+'>');
        win.document.close();
        win.print();
        win.close();
      }
    },
    printWindow() {
      window.print()
    },
    checkDate() {
      this.popover++
      if (this.dateStart.start && this.dateStart.end && this.popover >= 4) {

        this.pickDate = false
        this.popover = 0
      }
    },
    dontShowPromotion() {
      this.promotion = false
      window.localStorage.setItem("dontShowPromotion", true)
    },
    specificToggle(event) {
      event.currentTarget.parentElement.querySelector(".active")?.classList.remove("active")
      event.currentTarget.classList.toggle("active")
    },
    closeInfoPopup(id) {
      this.popups[id] = false
    },
    addNewGuests() {
      this.infoGuestsState = 'show'
      this.guests.push(this.newGuests)
      this.newGuests = {
        name: "",
        goverment: "",
        age: ""
      }
    },
    closeConfirmPopup(popup) {
      this.decied = popup
    },
    copy(event) {
      navigator.clipboard.writeText(event.currentTarget.previousSibling.querySelector(".chip__text").innerText)
    },
    share(event) {
      navigator.share({
        title: document.title,
        text: "Твой кабинет!",
        url: document.querySelector('link[rel=canonical]') ? document.querySelector('link[rel=canonical]').href : document.location.href,
      })
    }
  }
}
</script>

